.circular-button {
  position: absolute;
  left: 0;
  bottom: 0;
  background-color: var(--first-color);
  width: 165px;
  height: 165px;
  border-radius: 50%;
  text-decoration: none;
  z-index: 10;
}
.circular-button::after {
  content: '';
  position: absolute;
  width: 0%;
  height: 0%;
  background: #ebebeb;
  border-radius: 50%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: all 0.6s ease;
  z-index: 1;
}
.circular-button:hover span {
  color: var(--second-color);
}
.circular-button:hover::after {
  width: 100%;
  height: 100%;
}
.circular-button span {
  font-size: 21px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  text-align: center;
  transition: all 0.6s ease;
  z-index: 2;
  opacity: 0;
}
.button__buy-now {
  display: block;
}
.button__buy-now .bg-opacity-1::before {
  background: rgba(222,222,222,0.5);
}
.button__buy-now .bg-opacity-2::before {
  background: rgba(209,6,128,0.3);
}
.button__buy-now a {
  text-decoration: none;
  font-size: 19.22px;
  font-weight: 500;
  text-transform: uppercase;
  line-height: 20px;
  display: block;
  position: relative;
}
.button__buy-now a span {
  position: relative;
  z-index: 2;
}
.button__buy-now a::before {
  content: '';
  display: block;
  position: absolute;
  left: -25px;
  z-index: -1;
  width: 60px;
  height: 60px;
  background: #dedede;
  top: 50%;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
  border-radius: 50px;
  -webkit-transition: all 0.4s ease;
  -o-transition: all 0.4s ease;
  transition: all 0.4s ease;
}
.button__buy-now a::after {
  content: attr(data-hover);
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  width: 0%;
  overflow: hidden;
  white-space: nowrap;
  -webkit-transition: width 0.4s ease;
  -o-transition: width 0.4s ease;
  transition: width 0.4s ease;
}
.button__buy-now a:hover::after {
  width: 100%;
}
.button__buy-now a:hover::before {
  width: calc(100% + 50px);
}
