.burger-menu {
  --burger-menu-radius: 59px;
  --primary-color-darker: #000;
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
  z-index: 1;
  display: block;
  width: var(--burger-menu-radius);
  height: var(--burger-menu-radius);
  outline: none;
  cursor: pointer;
}
.burger-menu .line {
  position: absolute;
  left: 0;
  width: 100%;
  height: 4px;
  background: rgba(43,61,79,0.3);
  border-radius: 12px;
  overflow: hidden;
  transition: 0.5s;
}
.burger-menu .line:nth-child(1) {
  top: 30%;
}
.burger-menu .line:nth-child(2) {
  top: 50%;
}
.burger-menu .line:nth-child(3) {
  top: 70%;
}
.burger-menu .line::after {
  position: absolute;
  content: '';
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: var(--primary-color-darker);
  transform: translateX(-100%);
  transition: 0.25s;
}
.burger-menu .line:nth-child(2)::after {
  transition-delay: 0.1s;
}
.burger-menu .line:nth-child(3)::after {
  transition-delay: 0.2s;
}
.burger-menu:hover .line::after {
  transform: translateX(0);
}
.full-screen-menu {
  position: fixed;
  z-index: 9999;
  top: 0;
  right: 0;
  width: 100%;
  max-width: 530px;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: var(--first-color);
  opacity: 0;
  visibility: hidden;
  transition: 0.3s;
  transform: translateX(100%);
  -webkit-transform: translateX(100%);
  -moz-transform: translateX(100%);
  -o-transform: translateX(100%);
}
.full-screen-menu.checked-full-screen-menu {
  opacity: 1;
  visibility: visible;
  transform: translateX(0);
  -webkit-transform: translateX(0);
  -moz-transform: translateX(0);
  -o-transform: translateX(0);
}
.full-screen-menu .wrap_middle {
  width: 100%;
  max-width: 290px;
  margin: 0 auto;
}
.full-screen-menu .wrap_middle ul {
  margin: 0;
  padding-left: 0;
