.full-screen-menu .close_menu {
  display: block;
  margin-right: 20px;
  position: absolute;
  z-index: 999;
  margin-top: 15px;
  width: 40px;
  height: 30px;
  top: 0px;
  right: 0px;
}
.full-screen-menu .close_menu span {
  position: absolute;
  height: 3px;
  top: 50%;
  width: 100%;
  transition: 0.5s all ease;
  -webkit-transition: 0.5s all ease;
  -moz-transition: 0.5s all ease;
  -o-transition: 0.5s all ease;
  background-color: var(--third-color);
  right: 0px;
}
.full-screen-menu .close_menu span:before {
  position: absolute;
  content: '';
  width: 100%;
  height: 3px;
  background: var(--third-color);
  top: 10px;
  transition: 0.5s all ease;
  -webkit-transition: 0.5s all ease;
  -moz-transition: 0.5s all ease;
  -o-transition: 0.5s all ease;
  right: 0px;
}
.full-screen-menu .close_menu span:after {
  position: absolute;
  content: '';
  width: 100%;
  height: 3px;
  background: var(--third-color);
  top: -10px;
  transition: 0.5s all ease;
  -webkit-transition: 0.5s all ease;
  -moz-transition: 0.5s all ease;
  -o-transition: 0.5s all ease;
  right: 0px;
}
.full-screen-menu .close_menu span.is-active,
.full-screen-menu .close_menu span span {
  background-color: transparent;
}
.full-screen-menu .close_menu span.is-active:after,
.full-screen-menu .close_menu span span:after {
  transform: rotate(-45deg);
  top: 0px;
}
.full-screen-menu .close_menu span.is-active:before,
.full-screen-menu .close_menu span span:before {
  transform: rotate(45deg);
  top: 0px;
}
.checked-full-screen-menu {
  opacity: 1;
  visibility: visible;
  z-index: 1000;
}
.checked-full-screen-menu .fsm__nav-link span div,
.checked-full-screen-menu img,
.checked-full-screen-menu .t__title p {
  transform: translateY(0) !important;
  transition: 1.2s 0.1s cubic-bezier(0.35, 0, 0.07, 1) !important;
}
.checked-full-screen-menu .fsm__image-link:nth-child(1) img {
  transition-delay: 0.18s !important;
}
.checked-full-screen-menu .fsm__image-link:nth-child(2) img {
  transition-delay: 0.26s !important;
}
.checked-full-screen-menu .fsm__image-link:nth-child(3) img {
  transition-delay: 0.34s !important;
}
.checked-full-screen-menu .fsm__image-link:nth-child(4) img {
  transition-delay: 0.42s !important;
}
