.hamburger {
  display: block;
  position: absolute;
  z-index: 99;
  width: 40px;
  height: 30px;
  top: 0px;
  right: -40px;
  bottom: 0;
  margin: auto;
}
@media only screen and (max-width: 1100px) {
  .hamburger {
    right: 0;
  }
}
@media only screen and (max-width: 960px) {
  .hamburger {
    top: -4px;
    right: 15px;
  }
}
@media only screen and (max-width: 960px) {
  .hamburger {
    right: 10px;
    width: 35px;
  }
}
.hamburger span {
  position: absolute;
  height: 3px;
  top: 50%;
  width: 100%;
  transition: 0.5s all ease;
  -webkit-transition: 0.5s all ease;
  -moz-transition: 0.5s all ease;
  -o-transition: 0.5s all ease;
  background-color: var(--third-color);
  right: 0px;
  border-radius: 5px;
}
.hamburger span::before {
  position: absolute;
  content: '';
  width: 100%;
  height: 3px;
  background: var(--third-color);
  top: 10px;
  transition: 0.5s all ease;
  -webkit-transition: 0.5s all ease;
  -moz-transition: 0.5s all ease;
  -o-transition: 0.5s all ease;
  right: 0px;
  border-radius: 5px;
}
.hamburger span::after {
  position: absolute;
  content: '';
  width: 100%;
  height: 3px;
  background: var(--third-color);
  top: -10px;
  transition: 0.5s all ease;
  -webkit-transition: 0.5s all ease;
  -moz-transition: 0.5s all ease;
  -o-transition: 0.5s all ease;
  right: 0px;
  border-radius: 5px;
}
