.footer-extended-version {
  padding: 14px 0 18px 0;
}
.footer-extended-version ul.footer__wrapper {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  gap: 20px;
}
.footer-extended-version ul.footer__wrapper li,
.footer-extended-version ul.footer__wrapper a {
  color: #000;
}
.footer-extended-version ul.footer__wrapper li {
  position: relative;
  font-size: 18px;
  text-transform: uppercase;
}
.footer-extended-version ul.footer__wrapper li::before {
  content: '';
  position: absolute;
  top: 50%;
  right: -10px;
  transform: translateY(-50%);
  background-color: #000;
  width: 2px;
  height: 85%;
}
.footer-extended-version ul.footer__wrapper li:nth-last-child(1)::before {
  display: none;
}
.footer-extended-version ul.footer__wrapper li:hover a {
  text-decoration: underline;
}
@media only screen and (max-width: 1499px) {
  .footer-extended-version ul.footer__wrapper li {
    font-size: 15.8px;
  }
}
@media only screen and (max-width: 991px) {
  .footer-extended-version {
    padding: 13px 0 18px 0;
  }
  .footer-extended-version ul.footer__wrapper {
    gap: 15px;
  }
  .footer-extended-version ul.footer__wrapper li {
    font-size: 12.8px;
  }
  .footer-extended-version ul.footer__wrapper li::before {
    right: -9.5px;
    height: 82%;
  }
}
@media only screen and (max-width: 767px) {
  .footer-extended-version {
    padding: 13.5px 0;
  }
  .footer-extended-version ul.footer__wrapper {
    flex-wrap: wrap;
    row-gap: 5px;
  }
  .footer-extended-version ul.footer__wrapper li {
    font-size: 15.2px;
  }
  .footer-extended-version ul.footer__wrapper li::before {
    right: -9.5px;
    width: 1px;
    height: 82%;
  }
  .footer-extended-version ul.footer__wrapper li:nth-child(1) {
    width: 100%;
  }
  .footer-extended-version ul.footer__wrapper li:nth-child(1)::before {
    display: none;
  }
}
@media only screen and (max-width: 400px) {
  .footer-extended-version {
    padding: 13px 0;
  }
  .footer-extended-version ul.footer__wrapper {
    gap: 12px;
    row-gap: 5px;
  }
  .footer-extended-version ul.footer__wrapper li {
    font-size: 13.8px;
  }
  .footer-extended-version ul.footer__wrapper li::before {
    right: -7px;
  }
}
